import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import * as serviceWorker from './serviceWorker';
import $ from 'jquery';
import {
    View,
    Panel,
    PanelHeader,
    Cell,
    Group,
    PanelHeaderBack,
    Card, CardGrid, Avatar, Button, Header, FixedLayout, ModalRoot, ModalCard
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import bridge from '@vkontakte/vk-bridge';
import IconSad from "./components/emoji/IconSad";
import IconClown from "./components/emoji/IconClown";
import IconRobot from "./components/emoji/IconRobot";
import randInt from "./functions/randInt";
import getKeyValue from "./functions/getKeyValue";
import getUid from "./functions/getUid";
import fire from './firebase_config';
import Stats from "./components/panels/Stats";
import Menu from "./components/panels/Menu";
import WithBot from "./components/panels/WithBot";
import Modal from "./components/Modal";
import WithFriend from "./components/panels/withFriend";

class Main extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            activePanel: 'menu',
            leftHand: 0,
            rightHand: 0,
            userScore: 0,
            botScore: 0,
            activeModal: null,
            round: false,
            stats: {
                wins: 0,
                loses: 0
            },
            friendMatch: 0,
            invites: 0
        };
        this.stateWithBot = this.stateWithBot.bind(this);
        this.menu = this.menu.bind(this);
        this.setActivePanel = this.setActivePanel.bind(this);
    }

    menu(){
        this.setState({
            activePanel: 'menu'
        })
    }

    componentDidMount() {
        bridge.send("VKWebAppInit", {}).then(
            r => {
                bridge.send("VKWebAppGetUserInfo", {}).then(
                    r => {
                        console.log(r);
                        this.setState({
                            userAva: r.photo_200,
                            userName: r.first_name,
                            userId: r.id
                        });
                        console.log(this.state);
                        fire.firestore().collection('users').doc(r.id.toString()).onSnapshot((snapshot => {
                            //console.log(snapshot);
                            if(!snapshot.exists){
                                fire.firestore().collection('users').doc(r.id.toString()).set({
                                    balance: 0,
                                    wins: 0,
                                    loses: 0
                                }).then(r => console.log('done'));
                                fire.firestore().collection('invites').doc(r.id.toString()).set({}).then(r => console.log('done'));
                            }
                            fire.firestore().collection('invites').doc(r.id.toString()).onSnapshot((snap => {
                                let d = snap.data();
                                this.setState({
                                    invites: d.room
                                })
                            }));
                            let userData = snapshot.data();
                            this.setState({
                                stats: {
                                    wins: userData.wins,
                                    loses: userData.loses
                                }
                            })
                        }));
                    }
                );
            }
        );
        window.addEventListener('popstate', () => {
            if(this.state.activePanel !== 'menu'){
                this.setState({
                    activePanel: 'menu'
                })
            }
            else{
                window.history.back();
            }
        })
    }

    setActivePanel(p){
        this.setState({
            activePanel: p
        })
    }

    stateWithBot(p){
        //leftHand, rightHand, userScore, botScore, round, activeModal, stats
        this.setState({
            leftHand: p[0],
            rightHand: p[1],
            userScore: p[2],
            botScore: p[3],
            round: p[4],
            activeModal: p[5],
            stats: p[6]
        });
    }

    setActiveModal = (e) => {
        this.setState({
            activeModal: e,
            round: false
        })
    }

    render() {
        return (
            <View activePanel={this.state.activePanel} id="menu" modal={<Modal state={this.state} setActiveModal={this.setActiveModal}/>}>
                <Panel id="menu">
                    <Menu state={this.state} panel={this.setActivePanel}/>
                </Panel>
                <Panel id="stats">
                    <Stats state={this.state} menu={this.menu}/>
                </Panel>
                <Panel id="with_bot">
                    <WithBot state={this.state} menu={this.menu} return={this.stateWithBot}/>
                </Panel>
                <Panel id="with_friend">
                    <WithFriend state={this.state} menu={this.menu} return={this.stateWithBot}/>
                </Panel>
                <Panel id="invites">
                    <WithFriend state={this.state} menu={this.menu} return={this.stateWithBot}/>
                </Panel>
            </View>
        )
    }
}

ReactDOM.render(
  <React.StrictMode>
      <Main/>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWAd
serviceWorker.unregister();
