import {Cell, Group, Panel, PanelHeader, PanelHeaderBack} from "@vkontakte/vkui";
import Icon24ThumbUp from '@vkontakte/icons/dist/24/thumb_up';
import Icon24ThumbDown from '@vkontakte/icons/dist/24/thumb_down';
import React from "react";

const Stats = (e) => {
    return (
        <div>
            <PanelHeader left={<PanelHeaderBack onClick={() => e.menu()}/>}>Статистика</PanelHeader>
            <Group>
                <Cell before={<Icon24ThumbUp/>}>Побед: {e.state.stats.wins}</Cell>
                <Cell before={<Icon24ThumbDown/>}>Поражений: {e.state.stats.loses}</Cell>
            </Group>
        </div>
    )
}

export default Stats;