import {ModalCard, ModalRoot} from "@vkontakte/vkui";
import IconClown from "./emoji/IconClown";
import IconSad from "./emoji/IconSad";
import React from "react";

const Modal = (e) => {
    return (<ModalRoot activeModal={e.state.activeModal}
                       onClose={() => e.setActiveModal(null)}>
        <ModalCard
            id="userWon"
            onClose={() => e.setActiveModal(null)}
            icon={<IconClown size={128}/>}
            header="Победа"
            caption="Можем повторить"
            // actions={[{
            //     title: 'Попробовать',
            //     mode: 'primary',
            //     action: () => {
            //         this.setActiveModal(null);
            //     }
            // }]}
        >
        </ModalCard>
        <ModalCard
            id="botWon"
            onClose={() => e.setActiveModal(null)}
            icon={(
                <IconSad size={128}/>
            )}
            header="Проиграл"
            caption="лох"
            // actions={[{
            //     title: 'Попробовать',
            //     mode: 'primary',
            //     action: () => {
            //         this.setActiveModal(null);
            //     }
            // }]}
        >
        </ModalCard>
    </ModalRoot>)
}

export default Modal;