import {
    Avatar,
    Card,
    CardGrid,
    FixedLayout,
    Group,
    Header,
    PanelHeader,
    PanelHeaderBack
} from "@vkontakte/vkui";
import IconRobot from "../emoji/IconRobot";
import React from "react";
import $ from "jquery";
import randInt from "../../functions/randInt";
import bridge from "@vkontakte/vk-bridge";
import fire from "../../firebase_config";

const urls = [
    'https://emojipedia-us.s3.dualstack.us-west-1.amazonaws.com/thumbs/120/apple/237/raised-fist_270a.png',
    'https://emojipedia-us.s3.dualstack.us-west-1.amazonaws.com/thumbs/120/apple/237/victory-hand_270c.png',
    'https://emojipedia-us.s3.dualstack.us-west-1.amazonaws.com/thumbs/120/apple/237/raised-hand_270b.png'
];

class WithBot extends React.Component {
    constructor(props) {
        super(props);
    }

    jopa(e){
        if (this.props.state.round){
            return;
        }
        $('.left_hand,.right_hand').removeClass('animated');
        setTimeout(() => {
            $('.left_hand,.right_hand').addClass('animated');
        }, 5);
        this.props.return([0,0,this.props.state.userScore,this.props.state.botScore,true,this.props.state.activeModal,this.props.state.stats]);
        setTimeout(() => {
            let botScore = this.props.state.botScore;
            let userScore = this.props.state.userScore;
            let botChoice = randInt(0,2);
            let userChoice = e;

            switch (userChoice) {
                case 0:
                    switch (botChoice) {
                        case 1:
                            userScore++;
                            break;
                        case 2:
                            botScore++;
                            break;
                        default:
                            break;
                    }
                    break;
                case 1:
                    switch (botChoice) {
                        case 2:
                            userScore++;
                            break;
                        case 0:
                            botScore++;
                            break;
                        default:
                            break;
                    }
                    break;
                case 2:
                    switch (botChoice) {
                        case 0:
                            userScore++;
                            break;
                        case 1:
                            botScore++;
                            break;
                        default:
                            break;
                    }
                    break;
            }
            let round;
            if (userScore >= 2 || botScore >= 2){
                round = true;
            }
            else{
                round = false;
            }
            this.props.return([botChoice,userChoice,userScore,botScore,round,this.props.state.activeModal,this.props.state.stats]);
            $('.test').addClass('hidden');
            setTimeout(() => {
                $('.test2').removeClass('hidden');
            }, 200);
            setTimeout(() => {
                $('.test2').addClass('hidden');
                $('.test').removeClass('hidden');
                if (this.props.state.userScore >= 2 || this.props.state.botScore >= 2){
                    let won;
                    let wins = this.props.state.stats.wins;
                    let loses = this.props.state.stats.loses;
                    if (this.props.state.userScore >= 2){
                        won = 'userWon';
                        //bridge.send("VKWebAppStorageSet", {"key": "wins", "value": (parseInt(wins)+1).toString()});
                        wins++;
                    }
                    else{
                        won = 'botWon';
                        //bridge.send("VKWebAppStorageSet", {"key": "loses", "value": (parseInt(loses)+1).toString()});
                        loses++;
                    }
                    this.props.return([this.props.state.leftHand,this.props.state.rightHand,0,0,this.props.state.round,won,{wins:wins,loses:loses}]);
                    fire.firestore().collection('users').doc(this.props.state.userId.toString()).update({
                        wins: wins,
                        loses: loses
                    }).then(r => console.log('done'));
                }
                this.props.return([0,0,this.props.state.userScore,this.props.state.botScore,this.props.state.round,this.props.state.activeModal,this.props.state.stats]);
            }, 2000);
        }, 0.6*2.85*1000)
    }

    render() {
        return (<div>
            <PanelHeader left={<PanelHeaderBack onClick={() => this.props.menu()}/>}>Против бота</PanelHeader>
            <Group>
                <CardGrid className="battle_avas" style={{borderRadius: 5, padding: 10}}>
                    <Card size="l">
                        <Card style={{paddingTop: 15}} className="text-middle" size="s">
                            <div className="test">
                                <IconRobot size={48}/>
                                <Header className="no-flex" mode="secondary">полный лох</Header>
                            </div>
                            <div className="test2 hidden">{this.props.state.botScore}</div>
                        </Card>
                        <Card style={{marginTop: 42}} className="text-middle" size="s">
                            <Header className="no-flex" mode="secondary">против</Header>
                        </Card>
                        <Card style={{paddingTop: 15}} className="text-middle" size="s">
                            <div className="test">
                                <Avatar src={this.props.state.userAva ? this.props.state.userAva : 'https://vk.com/images/camera_200.png'}/>
                                <Header className="no-flex" mode="secondary">{this.props.state.userName ? this.props.state.userName : 'тоже лох'}</Header>
                            </div>
                            <div className="test2 hidden">{this.props.state.userScore}</div>
                        </Card>
                    </Card>
                    <div className="clear-fix"/>
                </CardGrid>
            </Group>
            <Group style={{padding: 30}}>
                <img className="left left_hand" alt="hui jopa pidaraz" src={urls[this.props.state.leftHand]}/>
                <img className="right right_hand" alt="hui jopa pidaraz" src={urls[this.props.state.rightHand]}/>
                <div className="clear-fix"/>
            </Group>
            <FixedLayout style={
                this.props.state.round ? {
                    transition: '.2s',
                    transform: 'translateY(100%)'
                } : {
                    transition: '.2s'
                }
            } vertical="bottom">
                <CardGrid className="battle_avas" style={{borderRadius: 5, padding: 10}}>
                    <Card size="l">
                        <Card className="text-middle" size="s">
                            <img onClick={() => {this.jopa(0)}} className="selectHand" alt="jopa" src={urls[0]}/>
                        </Card>
                        <Card className="text-middle" size="s">
                            <img onClick={() => {this.jopa(1)}} className="selectHand" alt="jopa" src={urls[1]}/>
                        </Card>
                        <Card className="text-middle" size="s">
                            <img onClick={() => {this.jopa(2)}} className="selectHand" alt="jopa" src={urls[2]}/>
                        </Card>
                    </Card>
                    <div className="clear-fix"/>
                </CardGrid>
            </FixedLayout>
        </div>)
    }
}

export default WithBot;