import {Cell, Group, Panel, PanelHeader, Counter} from "@vkontakte/vkui";
import React from "react";
import Icon28BrainOutline from '@vkontakte/icons/dist/28/brain_outline';
import Icon28StatisticsOutline from '@vkontakte/icons/dist/28/statistics_outline';
import Icon28UsersOutline from '@vkontakte/icons/dist/28/users_outline';
import Icon24AddOutline from '@vkontakte/icons/dist/24/add_outline';

const Menu = (e) => {
    return (
        <div>
            <PanelHeader>Камень, ножницы, бумага</PanelHeader>
            <Group>
                <Cell expandable before={<Icon28BrainOutline/>} onClick={() => {
                    e.panel('with_bot');
                    window.history.pushState(null, null, null);
                }}>Против бота</Cell>
                <Cell expandable before={<Icon28StatisticsOutline/>} onClick={() => {
                    e.panel('stats');
                    window.history.pushState(null, null, null);
                }}>Статистика</Cell>
                <Cell expandable before={<Icon28UsersOutline/>} onClick={() => {
                    e.panel('with_friend');
                    window.history.pushState(null, null, null);
                }}>Против друга</Cell>
                <Cell indicator={e.state.invites ? (<Counter mode="prominent">1</Counter>) : ''} expandable before={<Icon24AddOutline/>} onClick={() => {
                    e.panel('invites');
                    window.history.pushState(null, null, null);
                }}>Приглашения</Cell>
            </Group>
        </div>
    )
}

export default Menu;